<template>
  <div class="mains">
    	<h1>hell UsersList</h1>
    	<ul>
    		<!--:class="{hui:user.show}-->
    		<li v-for="user in users" @click="user.show = !user.show" :class="user.show?hui:kou">
    			<h2>{{ user.name }}</h2>
    			<h3 v-show="user.show">{{ user.position }}</h3>
    		</li>
    	</ul>
  </div>
</template>

<script>
export default {
  name: 'mains',
  data () {
    return {
    	hui:"hui",
    	kou:'',
    	users:[
	      {name:"张三",position:"web开发",show:false},
	      {name:"张三",position:"web开发",show:false},
	      {name:"张三",position:"web开发",show:false},
	      {name:"张三",position:"web开发",show:false},
	      {name:"张三",position:"web开发",show:false},
	      {name:"张三",position:"web开发",show:false}
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mains{
	width: 100%;
	max-width: 1200px;
	margin: 40 auto;
	padding: 0 20px;
	box-sizing: border-box;
}
.hui{
	background: royalblue;
	color: white;
}

h1{
	color: salmon;
}

ul{
	display: flex;
	flex-wrap: wrap;
	list-style-type: none;
	padding: 0;
}
li{
	flex-grow: 1;
	/*每个宽200px*/
	flex-basis: 200px;
	text-align: center;
	padding: 30px;
	border: 1px solid #222;
	background: transparent;
	color: #222222;
}
</style>



