<template>
	<div class="header">
		<h1>{{ msg }}</h1>
	</div>
</template>

<script>
	export default{
		name:"Header",
		data(){
			return{
				msg:"我的vue小demo！"
			}
		}
	}
</script>

<style scoped>
	.header{
		width: 100%;
		height: 100px;
		line-height: 100px;
		background: springgreen;
	}
</style>


